 */
function deleteSelectZone() {
  ElMessageBox.confirm("此操作将永久删除该域名, 是否继续?", "批量删除域名提示", {
    confirmButtonText: "确定",
    cancelButtonText: "取消",
    type: "warning",
  })
    .then(() => {
