        return;
      }
      // 获取域名数组，遍历发送请求
      control.isDialog = true
      pageData.selectList.forEach(async val => {
        const item = reactive({
                name: val.name,
                success: true,
                message: ''
              })
        pageData.resData.push(item)
        // 发送请求，显示结果
        let res = await deleteZone(item.name)
        if (!res) return
        item.message = res.data.message
        if (!res.data.success) {
          item.success = false
        }
      })
      ElMessage.info(`删除 ${pageData.resData.length} 条域名`)
      // 重新加载数据
      getZones()
    })
