}

async function download() {
  // 初始化
  control.isLoading = true
  // 转为普通对象
  const form = toRaw(zoneForm)
  // 将状态转为正确格式
  for (const key in statusList) {
    if (statusList[key] == form.status) {
      form.status = key
      break
    }
  }
  await downloadZone(form)
  control.isLoading = false
  ElMessage.success('下载成功')
}
