        name,
        success: true,
        message: ''
      })
      pageData.resData.push(item)
      // 发起请求
      let res
      if (type === 1) {
        res = await updateAutoHttps(item.name, formData.auto)
      }else if(type === 2) {
        res = await updateAlwaysUseHttps(item.name, formData.always)
      }else{
        res = await updateSSL(item.name, formData.ssl)
