        else if (msg == 'flexible') msg = '灵活'
        else if (msg == 'full') msg = '完全'
        else if (msg == 'strict') msg = '严格'
        res.data.message = msg
      }
      if (!res) return
      item.message = res.data.message
      if (!res.data.success) {
        item.success = false
      }
    })
  })
}

